import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ScullyRoute } from '@scullyio/ng-lib';
import { Observable } from 'rxjs';
import { map, pluck, switchMap, tap } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { BlogListService } from './blog-list.service';

@Component({
  selector: 'app-blog-list',
  templateUrl: './blog-list.component.html',
  styleUrls: ['./blog-list.component.scss'],
})
export class BlogListComponent implements OnInit {
  pageIndex = 1;
  pageSize = 5;

  assetsUrl = environment.assetsUrl;

  page$ = this.activatedRoute.queryParams.pipe(
    pluck('page'),
    map((page: number) => page || 1)
  );

  links$: Observable<ScullyRoute[]> = this.page$.pipe(
    switchMap((page: number) => {
      return this.blogListService.blogAllList$.pipe(
        map((list) => {
          console.log('@@@@', page);
          this.pageIndex = +page;
          return list.slice((page - 1) * this.pageSize, page * this.pageSize);
        })
      );
    })
  );

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    public blogListService: BlogListService
  ) {}

  ngOnInit(): void {
    // this.activatedRoute.queryParams.pipe(pluck('page')).subscribe((res) => {
    //   console.log('sadd', res);
    // });
    // this.blogListService.blogAllList$.subscribe((res) => {
    //   console.log(
    //     'page',res
    //   );
    // });
  }

  getPageIndex(index: number) {
    console.log('getPageIndex', index);
    this.pageIndex = index;
    this.router.navigate(['blog'], {
      queryParams: {
        page: index,
      },
    });

    // console.log('getPageIndex', index, index + this.pageSize);

    // for (let i = index; i < i + this.pageSize; i++) {
    //   console.log('asd', i)
    // }

    // this.blogListService.blogAllList$.subscribe((res) => {
    //   console.log(
    //     'page',
    //     res.slice(index - 1) * this.pageSize,
    //     index * this.pageSize
    //   );
    // });
  }
}
